@import "../../styles/helpers/_helpers";

.store-traffic {
	display: grid;
	grid-template-columns: repeat(10,1fr);
	gap: var(--grid-gap);
	margin-top: rem(60px);
	h2 {
		text-align: center;
		font-size: rem(26px);
		margin: 0;
		grid-column: 1/-1;
	}
	.field-title {
		font-weight: 600;
	}
}
.store-traffic__item {
	grid-column: span 2;
	text-align: center;
	display: grid;
	gap: rem(8px);
	position: relative;
	&:after {
		content: "+";
		position: absolute;
		top: rem(28px);
		right: rem(-24px);
		width: rem(24px);
		height: rem(24px);
		font-weight: 700;
		font-size: rem(18px);
	}
	@include last(1) {
		&:after {
			display: none;
		}
	}
	&:nth-of-type(4) {
		&:after {
			content: "=";
		}
	}
}